import React, { useEffect, useState } from 'react'
import axios from 'axios';
import Swal from 'sweetalert2';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import ProgressBar from 'react-bootstrap/ProgressBar';
import './App.css';
import { AiOutlineDelete } from "react-icons/ai";
import { VscEdit } from "react-icons/vsc"

const App = () => {

  const [now, setNow] = useState(0)
  const [pors, setPors] = useState(0)
  const [task, setTask] = useState();
  const [taskt, setTaskt] = useState();
  const [tasks, setTasks] = useState({
    title: "",
    description: "",
    completed: false,
    prioridad: ""
  });
  const [show, setShow] = useState(false);
  const [showe, setShowe] = useState(false);
  const [id, setId] = useState()

  useEffect(() => {
    conexion()
  }, []);

  const conexion = () => {
    axios.get('https://taskj.vercel.app/api/taskpend').then((res) => {
      setTaskt(res.data)
      setPors(Math.round((res.data.filter(data => data.completed === true).length) / res.data.length * 100))
      setNow(res.data.filter(data => data.completed === true).length)
      setTask((res.data.filter(data => data.prioridad === "alta").concat(res.data.filter(data => data.prioridad === "media"), res.data.filter(data => data.prioridad === "baja"), res.data.filter(data => data.prioridad === ""))).filter(data => data.completed === false))
    })
  }

  const handleClose = () => {
    setShow(false)
    setShowe(false)
  };

  const deleted = (id) => {
    Swal.fire({
      title: 'Ests seguro?',
      text: "No podrs revertir esto!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Si, Borrar!'
    }).then(async (result) => {
      if (result.isConfirmed) {
        await axios.delete(`https://taskj.vercel.app/api/deltaskpend/${id}`)
          .then(res => {
            Swal.fire({
              icon: 'success',
              title: 'Your file has been deleted.',
              showConfirmButton: false,
              timer: 1200
            })
          })
        conexion()
      }
    })
  }

  const create = () => {
    setShow(true)
  }

  const guardar = async (e) => {
    e.preventDefault();
    // await axios.post(`http://localhost:4002/api/taskpend`, tasks)
    await axios.post(`https://taskj.vercel.app/api/taskpend`, tasks)
      .then(res => {
        Swal.fire({
          icon: 'success',
          title: 'correcto',
          showConfirmButton: false,
          timer: 1200
        })
        setShow(false)
        conexion()
      })
  }

  const handleChange = (e) => {
    setTasks({
      ...tasks,
      [e.target.name]: e.target.value,
    });
  };

  const showedit = async (id) => {
    setId(id)
    await axios.get(`https://taskj.vercel.app/api/edit-taskpend/${id}`)
      .then(res => {
        console.log(res.data)
        setTasks(res.data)
      })
    setShowe(true)
  }

  const edit = async (e) => {
    e.preventDefault();
    await axios.put(`https://taskj.vercel.app/api/update-taskpend/${id}`, tasks)
      // await axios.put(`http://localhost:4002/api/update-taskpend/${id}`, tasks)
      .then(res => {
        Swal.fire({
          icon: 'success',
          title: 'Actualizado',
          showConfirmButton: false,
          timer: 1200
        })
      })
    setShowe(false)
    conexion()
  }

  const checked = async (id) => {
    await axios.get(`https://taskj.vercel.app/api/edit-taskpend/${id}`)
      .then(res => {
        if (res.data.completed === false) {
          res.data.completed = true

        } else {
          res.data.completed = false
        }
        axios.put(`https://taskj.vercel.app/api/update-taskpend/${id}`, res.data)
          // await axios.put(`http://localhost:4002/api/update-taskpend/${id}`, tasks)
          .then(res => {
            Swal.fire({
              icon: 'success',
              title: 'Actualizado',
              showConfirmButton: false,
              timer: 1200
            })
            conexion()
          })
      })
  }


  if (!task) return null;
  return (
    <div className='justify-content-center align-items-center' >
      <header>
        <header className="d-flex d-sm-flex justify-content-between px-3">
          <h2 className='nav'>Tasks ({task.length})</h2>
          <div className='w-50 mt-2 justify-content-center align-items-center'>
            <ProgressBar now={now} label={`${pors}%`} max={taskt.length} />;
          </div>
          <button
            to="/create-task"
            className="btn create btn-primary p-1 text-end"
            onClick={create}
          >
            Create Task
          </button>
        </header>
      </header>
      <div className="card-group justify-content-center">
        {task.map((task) => (
          <div key={task._id} className="m-2 d-flex justify-content-center">

            <div className="card text-start text-white p-1 " style={{ minWidth: "18rem", maxWidth: "18rem", backgroundColor: "#252525" }}>
              <div className='m-1 '>
                <div className='d-flex justify-content-between mx-2 '>
                  <h5 className='text-center '>{task.title}</h5>
                  <div>
                    <button className=" btn btn-secondary btn-sm m-1" onClick={showedit.bind(this, task._id)} >
                      <VscEdit />
                    </button>
                    <button className=" btn btn-danger btn-sm" onClick={deleted.bind(this, task._id)}>
                      <AiOutlineDelete />
                    </button>
                  </div>
                </div>
                <p className="font-italic m-0 px-2">{task.description}</p>
                <div className='d-flex justify-content-between'>
                  <div className='text-secondary '>
                    <p className="m-0 px-2"> Priority: {task.prioridad}</p>
                  </div>
                  <div className='text-secondary'>
                    <input
                      type="checkbox"
                      name="completed"
                      checked={task.completed}
                      // value={task.completed === false ? true : false}
                      onChange={checked.bind(this, task._id)}
                    /> Completada
                    {/* <p className="card-text text-dark font-italic px-2">{task._id}</p> */}
                  </div>

                </div>
              </div>
            </div>
          </div>
        ))}
      </div>

      <Modal show={show} onHide={handleClose} >
        <Modal.Body className='bg-dark '>
          <form className="bg-dark p-4 text-white">
            <h4>Task:</h4>
            <input
              type="text"
              name="title"
              onChange={handleChange}
              value={task.title}
              className=" w-100 p-2 rounded mb-3 bg-secondary text-white "
              placeholder="Write a title"

            />
            <h4>
              Description:
            </h4>
            <label className='w-100'>
              <textarea
                type="text"
                name="description"
                onChange={handleChange}
                value={task.description}
                className=" w-100 p-2 rounded bg-secondary text-white"
                placeholder="Write a description"
              />
            </label>
            <div className='d-flex justify-content-between'>
              <div className='row'>
                <div className=" col-12 form-check">
                  <input
                    className="form-check-input"
                    type="radio"
                    name="prioridad"
                    id="flexRadioDefault1"
                    value={"alta"}
                    onChange={handleChange}
                  />
                  <label className="form-check-label" htmlFor="flexRadioDefault1">
                    Prioridad alta
                  </label>
                </div>
                <div className="col-12 form-check">
                  <input
                    className="form-check-input"
                    type="radio"
                    name="prioridad"
                    id="flexRadioDefault1"
                    value={"media"}
                    onChange={handleChange}
                  />
                  <label
                    className="form-check-label" htmlFor="flexRadioDefault1">
                    Prioridad media
                  </label>
                </div>
                <div className="col-12 form-check">
                  <input
                    className="form-check-input"
                    type="radio"
                    name="prioridad"
                    id="flexRadioDefault1"
                    value={"baja"}
                    onChange={handleChange}
                  />
                  <label className="form-check-label" htmlFor="flexRadioDefault1">
                    Prioridad baja
                  </label>
                </div>
              </div>
              <div className='d-flex justify-content-end p-2 bg-dark '>
                <Button className='m-1' variant="secondary" onClick={handleClose}>
                  Close
                </Button>
                <Button variant="primary" className='m-1' onClick={guardar} >
                  Save Changes
                </Button>
              </div>
            </div>
          </form>

        </Modal.Body>
      </Modal>

      <Modal show={showe} onHide={handleClose} >
        <Modal.Body className='bg-dark '>
          <form className="bg-dark p-4 text-white">
            <h4>Task:</h4>

            <input
              type="text"
              name="title"
              onChange={handleChange}
              value={tasks.title}
              className=" w-100 p-2 rounded mb-3 bg-secondary text-white "
              placeholder="Write a title"

            />
            <h4>
              Description:
            </h4>
            <label className='w-100'>
              <textarea
                type="text"
                name="description"
                onChange={handleChange}
                value={tasks.description}
                className=" w-100 p-2 rounded bg-secondary text-white"
                placeholder="Write a description"

              />
            </label>
            <div className='d-flex justify-content-between'>
              <div className='row'>
                <div className=" col-12 form-check">
                  <input
                    className="form-check-input"
                    type="radio"
                    name="prioridad"
                    id="flexRadioDefault1"
                    value={"alta"}
                    onChange={handleChange}
                  />
                  <label className="form-check-label" htmlFor="flexRadioDefault1">
                    Prioridad alta
                  </label>
                </div>
                <div className="col-12 form-check">
                  <input
                    className="form-check-input"
                    type="radio"
                    name="prioridad"
                    id="flexRadioDefault1"
                    value={"media"}
                    onChange={handleChange}
                  />
                  <label
                    className="form-check-label" htmlFor="flexRadioDefault1">
                    Prioridad media
                  </label>
                </div>
                <div className="col-12 form-check">
                  <input
                    className="form-check-input"
                    type="radio"
                    name="prioridad"
                    id="flexRadioDefault1"
                    value={"baja"}
                    onChange={handleChange}
                  />
                  <label className="form-check-label" htmlFor="flexRadioDefault1">
                    Prioridad baja
                  </label>
                </div>
              </div>


              <div className='d-flex justify-content-end p-2 bg-dark '>
                <Button className='m-1' variant="secondary" onClick={handleClose}>
                  Close
                </Button>
                <Button variant="primary" className='m-1' onClick={edit} >
                  Actualizar
                </Button>
              </div>
            </div>
          </form>
        </Modal.Body>
      </Modal>

    </div >

  )
}

export default App